import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Navtop from '@/components/Navtop'
import Home from '@/pages/Home'
import Hotlist from '@/pages/Hotlist'
import Me from '@/pages/Me'
import Focus from '@/pages/Focus'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/home',
      name: 'Home',
      component: Home
    }, {
      path: '/focus',
      name: 'Focus',
      component: Focus
    }, {
      path: '/test',
      name: 'Test',
      component: Navtop,
      children: [{
        path: '/helloworld',
        name: HelloWorld,
        component: HelloWorld
      }]
    }, {
      path: '/Hotlist',
      name: 'Hotlist',
      component: Hotlist
    }, {
      path: '/me',
      name: 'Me',
      component: Me
    }
  ]
})



