<script>
import { Icon } from 'iview'
export default {
  data: function () {
    return {
      userMsg: [
        {problem: '假如阿里巴巴和腾讯同时要录用你，你会选择去哪家公司？', name: 'Henry', picurl: require('../assets/1.jpg'), answer: '其实，看到这个问题，都会嘀咕一下，作为国内大佬型公司，这两大公司都很牛，都是数一数二的大公司，进哪个都不吃亏，待遇都很好。但是真要我选的话，我会选择阿里巴巴，'},
        {problem: '小米为什么不推出一个从配置到价格和华为mate一样的手机系列？', name: 'Bucky', picurl: require('../assets/3.jpg'), answer: '其实这个问题非常简单的就是，目前来说国产供应链体系的厂商们还没有冲击高价的影响力，也没有冲击高价的实力，国产厂商目前能够做到5000元档位的也就只有华为了。'},
        {problem: 'Java语言在未来会继续火下去吗？', name: 'Henry', picurl: require('../assets/2.jpg'), answer: 'JAVA语言作为一个有20年历史的编程语言，自然有自己的独特之处，在这20年里JAVA语言不断的被完善和更新，直到现在有一个完整的JAVA体系，'},
        {problem: '为什么荷兰能制造世界顶级的光刻机，却没有世界顶级的芯片公司？', name: 'Bucky', picurl: require('../assets/4.jpg'), answer: 'ASML是从飞利浦独立出来的，正是因为专注于光刻机技术的研发和制造，而不涉足芯片的制造领域，最终使ASML成为世界顶级的光刻机制造商。而美国的芯片大厂很多都是ASML的股东和客户，'},
        {problem: '亚马逊相比阿里巴巴，为什么利润相差如戏巨大？', name: 'Henry', picurl: require('../assets/6.jpg'), answer: '亚马逊和阿里巴巴代表了电子商务的两种模式，一种模式是前者的大平台卖货模式，一种是后者的建立一个百货商店来帮助更多的人卖货，所以一个是重资产，一个是轻资产。但是目前亚马逊的市值是阿里巴巴的2倍多，而且股价持续上涨。'},
        {problem: '美国2018年GDP为20.5万亿美元，这个能占世界GDP多大的比重？', name: 'Bucky', picurl: require('../assets/5.jpg'), answer: '我国2010年GDP为5.75万亿美元，超过了日本的5.39亿美元的经济总量，仅次于美国的15万亿，成为了世界第二大经济体。但当时的中国人均GDP在世界的位次只排在第95位。'},
        {problem: '假如阿里巴巴和腾讯同时要录用你，你会选择去哪家公司？', name: 'Bucky', picurl: require('../assets/1.jpg'), answer: '其实，看到这个问题，都会嘀咕一下，作为国内大佬型公司，这两大公司都很牛，都是数一数二的大公司，进哪个都不吃亏，待遇都很好。但是真要我选的话，我会选择阿里巴巴，'}
