                                <p style="font-size:15px;">《知否知否》是一个为各行各业的工作人员提供问题解答、信息交流的综合服务平台。</p>
                                <br/>
                                <p style="font-weight:bold;font-size:20px;">《知否知否》能给用户带来什么？</p>
                                <p style="font-size:15px;">《知否知否》能给用户许多优秀的答案，带来许多与您一样奋斗在各行各业的聪慧工作者。</p>
                                <br/>
                                <p style="font-weight:bold;font-size:20px;">《知否知否》与网上许多问答平台有什么亮点？</p>
                                <p style="font-size:15px;">现如今，网上的确有着不少的问答平台，但是这些问答平台并没有对许多的垂直内容进行进一步的细分，从而许多用户将问答平台看做一个热闹的社区而已。《知否知否》通过
                                  不断的细分领域，从而给用户带来更精细更专业的内容！《知否知否》能给用户许多优秀的答案，带来许多与您一样奋斗在各行各业的聪慧工作者。</p>
                                <br/>
                                <p style="font-weight:bold;font-size:20px;">《知否知否》是否会继续更新，并推出新的功能？</p>
                                <p style="font-size:15px;">现如今的《知否知否》仅为第一版，对于第一版中许多不够好的地方，后续会继续完善，并在后续会不断的更新并推出新的功能，敬请期待！</p>
                                <br/>
                                <p style="font-weight:bold;font-size:20px;">关于《知否知否》母公司：</p>
                                <p style="font-size:15px;">《知否知否》的母公司为中国红鲤鱼与绿鲤鱼与驴呀网络技术有限公司，这是一个未来的互联网公司，这里有着一群积极奉献，勤劳聪慧的年轻工作者。如果你对我们感兴趣，我们欢迎你的加入。</p>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
