            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
  data: function () {
    return {
      activeName: 'six',
      form: {
        name: '红鲤鱼与绿鲤鱼001号',
        delivery: false,
        desc: '啦啦啦呀呀呀哎哎哎'
      },
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  method: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    onSubmit () {
      console.log('submit!')
    }
  }
}
</script>



